code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-background {
  width: 600px;
  height: 900px;
  color: wheat;
  background: -webkit-linear-gradient(top left, #0a642a, rgb(0,0,0,0));
  background: linear-gradient(to bottom right, #0a642a, rgb(0,0,0,0));
  margin-left: calc(100% / 2 - 600px);
}
.main-menu {
    width: 530px;
    height: 900px;
    margin: 0 auto ;
    padding: 0px;
    text-align: center;
    border-width: 15px;
    border-style: solid;
    -webkit-border-image: -webkit-linear-gradient(top, wheat, rgba(0,0,0,0)) 1 100%;
            border-image: linear-gradient(to bottom, wheat, rgba(0,0,0,0)) 1 100%;
    font-size: 20px;
  }
  #pazzle {
    cursor: pointer;
  }
  .main-menu img {
    height: 170px;
  }
  .main-menu p:hover {
    text-shadow: 5px 5px 5px black;
  }
.achieve-left {
    margin: 20px;
    border-width: 3px;
    border-style: solid;
    -webkit-border-image: -webkit-linear-gradient(top left, black, rgba(0,0,0,0)) 1;
            border-image: linear-gradient(to bottom right, black, rgba(0,0,0,0)) 1;
}
.achieve-right {
    text-align: left;
}
.achieve {
    display: grid;
    grid-template-columns: 200px 600px;
    background: -webkit-linear-gradient(top left, #079237, #272e34);
    background: linear-gradient(to bottom right, #079237, #272e34);
    color: wheat;
    border: 3px solid black;
    margin: 10px;
}
.border {
    background: -webkit-linear-gradient(left, rgb(0,0,0,0.8), rgb(0,0,0,0) );
    background: linear-gradient(to right, rgb(0,0,0,0.8), rgb(0,0,0,0) );
    text-indent: 20px;
}
.achieve-left .border {
    text-indent: 0;
}
.achieve-logo {
    height: 90px;
    margin: 20px 0;
}
.revard {
    height: 30px;
    margin-top: 10px;
}
.big {
    font-size: 30px;
    color: white;
}
.achieve-left p {
    margin: 5px;
}
.compass {
    margin-bottom: -4px;
    margin-right: 5px;
}
.map img {
    max-width: 100%;
    max-height: 450px;
    display: block;
    margin: 0 auto;
}

.header {
    display: grid;
    border: 3px solid black;
    grid-template-columns: 1fr 2fr 1fr;
    background: -webkit-linear-gradient(bottom left, #8a6029,#272e34);
    background: linear-gradient(to top right, #8a6029,#272e34);
}
.header h1 {
    text-align: left;
}
.header img {
    margin-top: 10px;
}
.return-button button {
    width: 150px;
    height: 50px;
    margin-top: 15px;
    background: -webkit-linear-gradient(top left, rgb(117, 23, 23), rgb(49, 45, 45));
    background: linear-gradient(to bottom right, rgb(117, 23, 23), rgb(49, 45, 45));
    border-radius: 5px;
    color: wheat;
    border: 2px outset gray;
}
.menu {
    border: 3px solid black;
    border-top: none;
    height: 750px;
    overflow: auto;
}
.menu p {
    line-height: 30px;
    margin: 0;
    padding: 5px;
    cursor: pointer;
}
.menu :nth-child(even) {
    /* border: 1px solid black; */
    background: -webkit-linear-gradient(left, rgb(0,0,0,0.4), rgb(0,0,0,0) );
    background: linear-gradient(to right, rgb(0,0,0,0.4), rgb(0,0,0,0) );
}
.menu p:hover {
    text-shadow: 5px 5px 7px black;
}
::-webkit-scrollbar {
    width: 7px;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(48, 47, 47);
}
* {
    box-sizing: border-box;
  }
  .JumpingPazzles {
    width: 1200px;
    max-height: 900px;
    margin: 50px auto;
    text-align: center;
    color: wheat;
  }
  .main {
    display: grid;
    grid-template-columns: 1fr 3fr;
    background: -webkit-linear-gradient(top left, #8a6029,#272e34);
    background: linear-gradient(to bottom right, #8a6029,#272e34);
  }
* {
  box-sizing: border-box;
}
